.popup-callme h2 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 30px;
  font-weight: 400;
  font-style: normal;
  line-height: 42px;
  margin: 0 0 4px 0;
  color: #ffffff; }

.popup-callme form {
  display: flex;
  flex-direction: column; }

.popup-callme p {
  font-family: "PT Mono", "Courier New", monospace;
  font-size: 16px;
  font-weight: 400;
  font-style: normal;
  line-height: 22px;
  margin: 0 0 38px 0;
  color: #ffffff; }

.popup-callme input {
  font-family: "PT Mono", "Courier New", monospace;
  font-size: 16px;
  font-weight: 400;
  font-style: normal;
  line-height: 22px;
  width: 100%;
  margin-bottom: 20px;
  padding: 14px 15px;
  color: #ffffff;
  border: none;
  border-radius: 3px;
  background: rgba(4, 20, 39, 0.15); }

.popup-callme textarea {
  font-family: "PT Mono", "Courier New", monospace;
  font-size: 16px;
  font-weight: 400;
  font-style: normal;
  line-height: 22px;
  height: 155px;
  margin-bottom: 25px;
  padding: 24px 15px;
  color: #ffffff;
  border: none;
  border-radius: 3px;
  background: rgba(4, 20, 39, 0.15); }

.popup-callme input::placeholder,
.popup-callme textarea::placeholder {
  color: #ffffff; }

.popup-callme button[type=submit] {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 700;
  font-style: normal;
  line-height: 19px;
  width: 100%;
  height: 45px;
  margin-bottom: 20px;
  padding-top: 6px;
  cursor: pointer;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  border: none;
  border-radius: 3px;
  background: #041827; }

.popup-callme label {
  font-family: "PT Mono", "Courier New", monospace;
  font-size: 14px;
  font-weight: 400;
  font-style: normal;
  line-height: 16px;
  position: relative;
  padding-left: 34px;
  cursor: pointer;
  opacity: 0.8;
  color: #ffffff; }

.popup-callme label::before {
  position: absolute;
  top: 0;
  left: 9px;
  display: block;
  width: 15px;
  height: 15px;
  content: "";
  border: 1px solid #ffffff;
  border-radius: 2px; }

.popup-callme label::after {
  position: absolute;
  top: 4px;
  left: 12px;
  display: block;
  width: 7px;
  height: 4px;
  content: "";
  transform: rotate(-45deg);
  opacity: 0;
  border: 1px solid #ffffff;
  border-top: none;
  border-right: none; }

.popup-callme input[type="checkbox"]:checked + label::after {
  opacity: 1; }

@media (max-width: 770px) {
  .is-hidden--mobile {
    display: none; } }

.about-company-block__info p.is-hidden {
  display: none; }

@media (max-width: 770px) {
  .page-footer__wrapper h3.is-hidden::after {
    position: absolute;
    top: 20px;
    right: 33px;
    display: block;
    width: 1px;
    height: 15px;
    content: "";
    background-color: #ffffff; } }

.page-footer-link-list--first.is-hidden {
  display: none; }

.page-footer-link-list--second.is-hidden {
  display: none; }

.page-footer__contacts p.is-hidden {
  display: none; }

